// import Web3 from 'web3'
import {store} from '../store/'
// import {SelectedProvider} from '../util/constants/networks'
import {BigNumber} from 'bignumber.js'

let toFixedSpecial = function (base, n) {
  var str = base.toFixed(n)
  if (str.indexOf('e+') < 0) {
    return str
  }
  // if number is in scientific notation, pick (b)ase and (p)ower
  return str.replace('.', '').split('e+').reduce(function (p, b) {
    return p + Array(b - p.length + 2).join(0)
  }) + '.' + Array(n + 1).join(0)
}

let updateAwardEvents = async function () {
  console.log('updateAwardEvents...')
  let web3 = store.state.web3.web3Instance() // window.web3
  // web3 = new Web3(new Web3.providers.HttpProvider(SelectedProvider))
  if (/* web3 && */ store.state.web3.web3Instance /* && store.state.web3.AwardLastKnownBlockNumber < web3.eth.blockNumber */) {
    let LastBlock = store.state.web3.AwardLastKnownBlockNumber

    web3.eth.getBlockNumber(function (error, curBlockNumber) {
      if (error) {
        console.log('updateAwardEvents error', error)
      } else {
        store.state.ECBcontractInstance().Award({_from: store.state.web3.coinbase}, {fromBlock: LastBlock, toBlock: 'latest'}).get((error, logs) => {
          // event Award(address indexed _from, string _betKey, string _type, uint256 _stake, string _resCode, uint256 _resValue);
          let arr = []
          let key
          let diff
          logs.forEach(function (log) {
            key = log.args._betKey
            // console.log(key.indexOf('_', key.indexOf('_') + 1), key.indexOf('_') + 1, key)
            diff = key.indexOf('_', key.indexOf('_') + 1) - key.indexOf('_') - 1
            arr.push({
              pair: key.substr(0, key.indexOf('_')), // log.args._betKey.substr(0, 6),
              tf: key.substr(key.indexOf('_', key.indexOf('_')) + 1, diff), // log.args._betKey.substr(7, 3),
              bartime: log.args._betKey.substr(9 + diff, 4) + '-' + log.args._betKey.substr(13 + diff, 2) + '-' +
                log.args._betKey.substr(15 + diff, 2) + ' ' + log.args._betKey.substr(18 + diff, 2) + ':' +
                log.args._betKey.substr(20 + diff, 2) + ':' + log.args._betKey.substr(22 + diff, 2),
              type: log.args._type,
              stake: BigNumber(log.args._stake).toNumber() / 10 ** 18,
              resCode: log.args._resCode,
              value: BigNumber(log.args._resValue).toNumber() / 10 ** 18
            })
          })
          // console.log('writeAwards', 'fromBlock:', LastBlock, ' ', 'web3.eth.blockNumber', web3.eth.blockNumber, arr)
          if (error !== null) {
            console.log('error for writeAwards', error)
          } else {
            console.log('dispatch writeAwards', arr)
            store.dispatch('writeAwards', arr)
          }
        })
        store.dispatch('setAwardLastKnownBlockNumber', /* web3.eth.blockNumber + 1 */ curBlockNumber)
      }
    })
  }
}
let updateEvents = async function () {
  console.log('updateEvents...')
  let web3 = store.state.web3.web3Instance() // window.web3
  if (web3 /* && store.state.web3.web3Instance */) {
    let LastBlock = store.state.web3.LastKnownBlockNumber
    web3.eth.getBlockNumber(function (error, curBlockNumber) {
      console.log('error=', error, 'curBlockNumber=', curBlockNumber)
      if ((LastBlock === 0) && (curBlockNumber >= 1200)) { LastBlock = curBlockNumber - 1200 }
      store.state.OTcontractInstance().Tick({}, {fromBlock: LastBlock, toBlock: 'latest'}).get(function (error, logs) {
        console.log('get ticks from block', LastBlock, 'we need key ', store.state.web3.keyPairTimeFrameListSelectedcomplex)
        let key
        let key2
        logs.forEach(function (log) {
          key = log.args._betKey
          if (key.substr(0, key.indexOf('_', 8)) === store.state.web3.keyPairTimeFrameListSelectedcomplex) {
            key2 = key.substr(key.indexOf('_', 8) + 1)
            store.dispatch('writeAllTicksList', {
              key: key2.substr(0, 4) + '-' + key2.substr(4, 2) + '-' + key2.substr(6, 2) + ' ' + key2.substr(9, 2) + ':' + key2.substr(11, 2),
              O: BigNumber(log.args.O).toNumber() / 100,
              H: BigNumber(log.args.H).toNumber() / 100,
              L: BigNumber(log.args.L).toNumber() / 100,
              C: BigNumber(log.args.C).toNumber() / 100})
            // store.dispatch('writeBetKey', key)
          }
        })
        console.log(error)
      })
      console.log('after OTcontractInstance().Tick(')
      store.state.ECBcontractInstance().BetUp(
        {_from: store.state.web3.coinbase, _betKey: store.state.web3.betKey},
        {fromBlock: LastBlock, toBlock: 'latest'}
      ).get((error, logs) => {
        // event BetUp(address indexed _from, string _betKey, uint256 _resValue);
        let arr = []
        logs.forEach(log => arr.push({value: BigNumber(log.args._resValue).toNumber() / 10 ** 18}))
        store.dispatch('writeBetsUp', arr)
        if (error !== null) { console.log('error for writeBetsUp', error) }
      })
      store.state.ECBcontractInstance().BetDown(
        {_from: store.state.web3.coinbase, _betKey: store.state.web3.betKey},
        {fromBlock: LastBlock, toBlock: 'latest'}
      ).get((error, logs) => {
        // event BetUp(address indexed _from, string _betKey, uint256 _resValue);
        let arr = []
        logs.forEach(log => arr.push({value: BigNumber(log.args._resValue).toNumber() / 10 ** 18}))
        store.dispatch('writeBetsDown', arr)
        if (error !== null) { console.log('error for writeBetsDown', error) }
      })
      store.dispatch('setLastKnownBlockNumber', curBlockNumber)
    })
  }
}
let updateBetState = async function () {
  console.log('updateBetState...')
  store.state.ECBcontractInstance().getLastBarbyPairTF(store.state.web3.keyPairTimeFrameListSelectedcomplex, {from: store.state.web3.coinbase}, function (err, resBetKey) {
    if (err) {
      console.log(err)
    } else {
      store.dispatch('writeBetKey', resBetKey)
      store.state.ECBcontractInstance().GetBetState(store.state.web3.betKey, {from: store.state.web3.coinbase}, function (err, resBetKeyState) {
        if (err) {
          console.log(err)
        } else { store.dispatch('writeBetKeyState', resBetKeyState) }
      })

      store.state.ECBcontractInstance().GetMyBetUpper(store.state.web3.betKey, {from: store.state.web3.coinbase}, function (err, res) {
        if (err) {
          console.log(err)
        } else {
          let upperStakesWEIBigNumber = new BigNumber(res[1])
          let upperStakesWEI = upperStakesWEIBigNumber.toNumber()
          store.dispatch('setUpperStakes', upperStakesWEI)
        }
      })

      store.state.ECBcontractInstance().GetMyBetLower(store.state.web3.betKey, {from: store.state.web3.coinbase}, function (err, res) {
        if (err) {
          console.log(err)
        } else {
          let lowerStakesWEIBigNumber = new BigNumber(res[1])
          let lowerStakesWEI = lowerStakesWEIBigNumber.toNumber()
          store.dispatch('setLowerStakes', lowerStakesWEI)
        }
      })

      store.state.ECBcontractInstance().GetAllBetsUpper(store.state.web3.betKey, {from: store.state.web3.coinbase}, function (err, res) {
        if (err) {
          console.log(err)
        } else {
          let upperAllStakesWEIBigNumber = new BigNumber(res[0])
          let upperAllStakesWEI = upperAllStakesWEIBigNumber.toNumber()
          store.dispatch('setUpperAllStakes', upperAllStakesWEI)
        }
      })

      store.state.ECBcontractInstance().GetAllBetsLower(store.state.web3.betKey, {from: store.state.web3.coinbase}, function (err, res) {
        if (err) {
          console.log(err)
        } else {
          let lowerAllStakesWEIBigNumber = new BigNumber(res[0])
          let lowerAllStakesWEI = lowerAllStakesWEIBigNumber.toNumber()
          store.dispatch('setLowerAllStakes', lowerAllStakesWEI)
        }
      })
    }
  })
}
let updateState = /* async */ function () {
  console.log('updateState...')
  // console.log('web3.eth.coinbase !== store.state.web3.coinbase', store.state.web3.coinbase)
  // console.log('web3.eth.coinbase !== store.state.web3.coinbase', 'store.state.web3.coinbase=', store.state.web3.coinbase, 'web3=', store.state.web3.web3Instance)
  let newCoinbase = store.state.web3.coinbase
  let newAccountInput = store.state.web3.account_input
  /* web3. */ store.state.web3.web3Instance().eth.getBalance(newCoinbase, function (err, newBalance) {
    if (err) {
      console.log(err)
    } else {
      let newBalanceInt = BigNumber(newBalance.toString(10)).toNumber()
      // console.log('it is OK!  newBalanceInt = ', newBalanceInt)
      store.dispatch('pollWeb3', {
        coinbase: newCoinbase,
        balance: toFixedSpecial(newBalanceInt, 0),
        account_input: newAccountInput // changed
      })
    }
  })
  console.log('store.state.web3.coinbase', store.state.web3.coinbase)
  store.state.GTAcontractInstance().balanceOf(store.state.web3.coinbase, function (error, Newbalance) {
    if (error) {
      console.log(error)
    } else {
      let GTABallanceBigNumber = new BigNumber(Newbalance)
      let GTABallanceWEI = GTABallanceBigNumber.toNumber()
      store.dispatch('setGTAbalance', GTABallanceWEI)
    }
  })
  store.state.ECBcontractInstance().GetCurrentDepo({from: store.state.web3.coinbase}, function (error, Newbalance) {
    if (error) {
      console.log(error)
    } else {
      let BetGTABallanceBigNumber = new BigNumber(Newbalance)
      let BetGTABallanceWEI = BetGTABallanceBigNumber.toNumber()
      // console.log('it is OK!  BetGTABallanceWEI = ', BetGTABallanceWEI)
      store.dispatch('setBetGTAbalance', BetGTABallanceWEI)
    }
  })
  store.state.GTAcontractInstance().allowance(store.state.web3.coinbase, store.state.ECBcontractInstance().address, function (error, Newbalance) {
    if (error) {
      console.log(error)
    } else {
      let GTAtoECBallowanceWEIBigNumber = new BigNumber(Newbalance)
      let GTAtoECBallowanceWEI = GTAtoECBallowanceWEIBigNumber.toNumber()
      // console.log('it is OK!  GTAtoECBallowanceWEI = ', GTAtoECBallowanceWEI)
      store.dispatch('setGTAtoECBallowance', GTAtoECBallowanceWEI)
    }
  })
}
let pollWeb3 = async function () {
  updateState()
  updateEvents()
  updateBetState()
  updateAwardEvents()

  clearInterval(store.updateStateTimer)
  let updateStateTimer_ = setInterval(async () => { updateState() }, 9000) // 240 seconds 4 mins
  console.log('updateStateTimer 1--', updateStateTimer_)
  store.dispatch('writeUpdateStateTimerState', updateStateTimer_)
  // setInterval(async () => { updateState() }, 24000) // 240 seconds 4 mins
  clearInterval(store.updateEventsTimer)
  let updateEventsTimer_ = setInterval(async () => { updateEvents() }, 8000) // 15 seconds
  console.log('updateEventsTimer 1--', updateEventsTimer_)
  store.dispatch('writeUpdateEventsTimerState', updateEventsTimer_)
  // setInterval(async () => { updateEvents() }, 15000) // 15 seconds
  clearInterval(store.updateBetStateTimer)
  let updateBetStateTimer_ = setInterval(async () => { updateBetState() }, 7000) // 10 seconds
  console.log('updateBetStateTimer 1--', updateBetStateTimer_)
  store.dispatch('writeUpdateBetStateTimerState', updateBetStateTimer_)
  // setInterval(async () => { updateBetState() }, 10000) // 10 seconds
  clearInterval(store.updateAwardEventsTimer)
  let updateAwardEventsTimer_ = setInterval(async () => { updateAwardEvents() }, 10000) // 30 seconds
  console.log('updateAwardEventsTimer 1--', updateAwardEventsTimer_)
  store.dispatch('writeUpdateAwardEventsTimerState', updateAwardEventsTimer_)
  // setInterval(async () => { updateAwardEvents() }, 25000) // 30 seconds
}

// export default pollWeb3
export { pollWeb3, updateState, updateEvents, updateBetState, updateAwardEvents }



