<template>
  <div class='metamask-info'>
    <p v-if="isInjected" id="has-metamask"><i aria-hidden="true" class="fa fa-check"></i> Metamask installed</p>
    <p v-else id="no-metamask"><i aria-hidden="true" class="fa fa-times"></i> Metamask not found</p>
    <p>Network: {{ network }}</p>
    <p>Account: {{ coinbase }}</p>
    <p>Balance: {{ balance }} Wei // {{ ethBalance }} Eth</p>
    <h4>Please insert Account</h4>
    <p> Account: <input v-model="input_account" placeholder="0x account address"></p>
    <p> Private key: <input v-model="input_private" placeholder="0x private key"></p>
    <button class="btn" v-on:click="clickAccountInput">ok</button>
    <button class="btn" v-on:click="clickCalcBallance">Calc ballance</button>
    <p> from the state private_input: {{ private_input }} </p>
  </div>
</template>

<script>
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'
export default {
  data () {
    return {
      input_account: null,
      input_private: null,
      new_balance: null
    }
  },
  name: 'hello-metamask',
  computed: mapState({
    isInjected: state => state.web3.isInjected,
    network: state => NETWORKS[state.web3.networkId],
    coinbase: state => state.web3.coinbase,
    balance: state => state.web3.balance,
    ethBalance: state => {
      if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
    },
    // account_input: state => state.web3.account_input,
    private_input: state => state.web3.private_input
  }),
  methods: {
    clickAccountInput (event) {
      var accountKeys = {
        adr: this.input_account,
        prv: this.input_private
      }
      var accountKeysStr = JSON.stringify(accountKeys)
      localStorage.setItem('ethercall', accountKeysStr)
      console.log('Trying to set account', this.input_account, 'local storage is ', accountKeysStr)
      this.$store.input_account = this.input_account
      this.$store.dispatch('setWeb3Account', this.input_account)
      this.$store.dispatch('setWeb3Private', this.input_private)
      var accountKeysStr2 = localStorage.getItem('ethercall')
      if (accountKeysStr2 !== null) {
        var accountKeys2 = JSON.parse(accountKeysStr2)
        console.log('Read from local storage', accountKeys2, 'account=', accountKeys2.adr, 'private key=', accountKeys2.prv)
      }
      console.log('is there key keykey?', localStorage.getItem('keykey'))
    },
    clickCalcBallance  (event) {
      console.log('Trying to calc and get account balance ', this.$store.new_balance)
      this.$store.dispatch('getNewBalance')
    }
  }
}
</script>

<style scoped>
input {
  width: 300px;
}
.metamask-info {
  text-align:center;
}
#has-metamask {
  color: green;
}
#no-metamask {
  color:red;
}</style>



