<template>
  <div class='main'>
    <v-card flat color='brown darken-4'>
      <v-card-text class='py-2'>
        <v-layout row class='body-1 white--text text--darken-1'><b>Your transactions and stakes</b></v-layout>
      </v-card-text>
    </v-card>
    <v-tabs icons-and-text centered color='brown darken-2' dark grow>
    <v-tabs-slider color='deep-orange accent-4'></v-tabs-slider>
    <v-tab href='#tab-1'>Transactions<v-icon dark left>swap_horiz</v-icon></v-tab>
    <v-tab href='#tab-2'>Stakes<v-icon dark>money</v-icon></v-tab>
    <v-tab-item id='tab-1'>
      <v-card flat color='brown darken-3'>
        <v-data-table
          :headers='txHeaders'
          :items='transactions'
          dark
          class='elevation-1'>
          <template slot='items' slot-scope='props'>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.txHash }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.txType }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.GtaValue }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.EthValue }}</td>
          </template>
        </v-data-table>
        <!-- <div class="text-xs-center pt-2">
          <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
        </div> -->
      </v-card>
    </v-tab-item>
    <v-tab-item id='tab-2'>
      <v-card flat color='brown darken-3'>
        <v-data-table
          :headers='stakesHeaders'
          :items='stakes'
          dark
          isDescending
          class='elevation-2'>
          <template slot='items' slot-scope='props'>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.pair }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.tf }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.bartime }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.type }}</td>
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.stake }}</td>
            <td style="height:40px" :class="changeClass(props.item.resCode)">{{ props.item.resCode }}</td>
            <!--<td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.resCode }}</td>-->
            <td style="height:40px" class='text-xs-center brown darken-3'>{{ props.item.value }}</td>
          </template>
        </v-data-table>
      </v-card>
    </v-tab-item>
  </v-tabs>
  </div>
</template>

<script>
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'
import Tx from 'ethereumjs-tx'
export default {
  data () {
    return {
      pagination: {},
      input_account: null,
      new_balance: null,
      valueToDepo: 0,
      valueToWithdraw: 0,
      txHeaders: [
        { text: 'Transaction hash', value: 'txHash', align: 'center', sortable: false },
        { text: 'Type', value: 'txType', align: 'center', sortable: false },
        { text: 'BIN', value: 'GtaValue', align: 'center', sortable: false },
        { text: 'ETH', value: 'EthValue', align: 'center', sortable: false }
      ],
      stakesHeaders: [
        { text: 'Pair', align: 'center', sortable: false, value: 'pair' },
        { text: 'Time Frame', align: 'center', sortable: false, value: 'tf' },
        { text: 'Bar time', align: 'center', sortable: false, value: 'bartime' },
        { text: 'Type', value: 'type', align: 'center', sortable: false },
        { text: 'Stake', value: 'stake', align: 'center', sortable: false },
        { text: 'Result', value: 'result', align: 'center', sortable: false },
        { text: 'Result value', value: 'resultValue', align: 'center', sortable: false }
      ] /* ,
      stakes1: [
        {
          // value: false,
          pair: 'btcusd',
          tf: 'm15',
          bartime: '2017-11-30 22:10:00',
          type: 'call',
          stake: 3000,
          resCode: 'win',
          value: 3560
        },
        {
          // value: false,
          pair: 'btcusd',
          tf: 'm15',
          bartime: '2017-11-30 22:20:00',
          type: 'call',
          stake: 3000,
          resCode: 'win',
          value: 3560
        },
        {
          // value: false,
          pair: 'btcusd',
          tf: 'm15',
          bartime: '2017-11-30 22:30:00',
          type: 'call',
          stake: 3000,
          resCode: 'win',
          value: 3560
        },
        {
          // value: false,
          pair: 'btcusd',
          tf: 'm15',
          bartime: '2017-11-30 22:40:00',
          type: 'call',
          stake: 3000,
          resCode: 'win',
          value: 3560
        },
        {
          // value: false,
          pair: 'btcusd',
          tf: 'm15',
          bartime: '2017-11-30 22:50:00',
          type: 'call',
          stake: 3000,
          resCode: 'win',
          value: 3560
        },
        {
          // value: false,
          pair: 'btcusd',
          tf: 'm15',
          bartime: '2017-11-30 22:55:00',
          type: 'call',
          stake: 3000,
          resCode: 'win',
          value: 3560
        }
      ]
      */
    }
  },
  computed:
    mapState({
      /* pages: state => {
        console.log('state.web3.TransactionsList', state.web3.TransactionsList.length)
        if (state.web3.TransactionsList.length == null || state.web3.TransactionsList.length === 0) { return 1 }
        return Math.ceil(state.web3.TransactionsList.length / 5)
      }, */
      isInjected: state => state.web3.isInjected,
      network: state => NETWORKS[state.web3.networkId],
      coinbase: state => state.web3.coinbase,
      transactions: state => state.web3.TransactionsList,
      stakes: state => state.web3.Awards,
      GTAbalance: state => state.web3.GTAbalance,
      GTAbalanceNice: state => {
        if (state.web3.web3Instance !== null) {
          let _GTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.GTAbalance, 'ether')
          let _dotposition = _GTAbalanceEther.indexOf('.')
          if (_dotposition > 0) {
            return parseInt(_GTAbalanceEther.substring(0, _dotposition))
          } else {
            return parseInt(_GTAbalanceEther)
          }
        }
      },
      ECBcontractGTAbalance: state => state.web3.BetGTAbalance,
      ECBcontractGTAbalanceNice: state => {
        if (state.web3.web3Instance !== null) {
          let _ECBcontractGTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.BetGTAbalance, 'ether')
          let _dotposition = _ECBcontractGTAbalanceEther.indexOf('.')
          if (_dotposition > 0) {
            return parseInt(_ECBcontractGTAbalanceEther.substring(0, _dotposition))
          } else {
            return parseInt(_ECBcontractGTAbalanceEther)
          }
        }
      },
      GTAtoECBallowance: state => state.web3.GTAtoECBallowance
    }),
  methods: {
    changeClass (value) {
      return {
        'text-xs-center green darken-4': value === 'win',
        'text-xs-center brown darken-3': value === 'loos'
      }
    },
    sendTX (_from, _to, _value, _data) {
      let _count = this.$store.state.web3.web3Instance().eth.getTransactionCount(_from)
      let rawTransaction = {
        'from': _from,
        'nonce': this.$store.state.web3.web3Instance().toHex(_count),
        'gasPrice': this.$store.state.web3.web3Instance().toHex(this.$store.state.web3.gasPrice),
        'gasLimit': this.$store.state.web3.web3Instance().toHex(this.$store.state.web3.gasContractCall),
        'to': _to,
        'value': _value,
        'data': _data
      }
      let privateKey = this.$store.state.web3.private_input.substring(2)
      let privKey = Buffer.from(privateKey, 'hex')
      let tx = new Tx(rawTransaction)
      tx.sign(privKey)
      let serializedTx = tx.serialize()
      console.log('serializedTx ||||', serializedTx)
      let err, hash
      this.$store.state.web3.web3Instance().eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
        if (!err) {
          console.log('Sent to:' + _to + ' Transaction hash: ' + hash)
        } else {
          console.log('Not sent to:' + _to + ' Error in sending: ' + err)
        }
      })
      return (err, hash)
    }
  }
}
</script>

<style scoped>
.main {  max-height: 300px;}
input {  width: 300px;}
</style>



