<template>
  <v-dialog v-model="forgetAccountDialog" persistent max-width="700px" @keydown.esc="dialog = false">
    <v-card>
     <v-card-title>
       <span class="headline"><v-icon x-large color="red darken-1">warning</v-icon>Forget account?</span>
       <v-spacer></v-spacer>
       <v-card-text>You are about to remove an Ethereum account: </v-card-text>
       <v-card-text><code>{{input_account}}</code></v-card-text>
       <v-card-text><v-icon small color="red darken-1">warning</v-icon><b>To withdraw funds from this account in the future, you will need the Private Key. SAVE it now!</b></v-card-text>
       <v-card-text>Private Key: <code>{{input_private}}</code></v-card-text>
       <v-card-text>If you lose the Private Key, you cannot restore it.</v-card-text>
     </v-card-title>
     <v-card-actions>
       <v-spacer></v-spacer>
       <v-btn color="red darken-1" flat v-on:click="clickForget">Yes, forget account</v-btn>
       <v-btn color="blue darken-1" flat @click.native="forgetAccountDialog = false">Cancel</v-btn>
     </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import {mapState} from 'vuex'
export default {
  data: () => ({
    forgetAccountDialog: false
  }),
  computed: mapState({
    input_account: state => state.web3.account_input,
    input_private: state => state.web3.private_input
  }),
  methods: {
    open: function () {
      var vm = this
      if (!this.input_account || !this.input_private) return
      vm.forgetAccountDialog = true
    },
    clickForget (event) {
      if (!this.input_account || !this.input_private) return
      let AccountStore = JSON.parse(localStorage.getItem('ethercall2'))
      if (AccountStore === null) return
      var foundIndex = -1
      if (AccountStore !== null) {
        // There is account store
        for (var i = 0; i < AccountStore.accounts.length; i++) {
          console.log('AccountStore.accounts[i].adr', AccountStore.accounts[i].adr)
          if (AccountStore.accounts[i].adr === this.input_account) {
            foundIndex = i
            break
          }
        }
        if (foundIndex !== -1) {
          console.log('removing foundIndex ', foundIndex)
          AccountStore.accounts.splice(foundIndex, 1)
          console.log('AccountStore.accounts after delete', AccountStore.accounts)
        } else { return }

        if (AccountStore.accounts.length > 0) {
          AccountStore.selectedAccount = 0
          localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
          // Update list to the app store
          this.$store.dispatch('setAccountsList', AccountStore.accounts)
          // Set selected account to the first in list
          let AccountAddress = AccountStore.accounts[0].adr
          let AccountPrivate = AccountStore.accounts[0].prv
          this.$store.dispatch('setWeb3AccountPrivate', {AccountAddress, AccountPrivate})
        } else {
          console.log('no elements')
          localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
          // Update list to the app store
          this.$store.dispatch('setAccountsList', AccountStore.accounts)
          let AccountAddress = ''
          let AccountPrivate = ''
          this.$store.dispatch('setWeb3AccountPrivate', {AccountAddress, AccountPrivate})
        }
      } else {
        return
      }
      this.forgetAccountDialog = false
    }
  }
}
</script>



