             label="Address"
             hint="0x..." persistent-hint required>
             </v-text-field>
           </v-flex>
           <v-flex xs12>
             <v-text-field
             v-model="input_private"
             label="Private key"
             hint="0x..." persistent-hint required></v-text-field>
           </v-flex>
           <v-flex xs12 sm6>
           </v-flex>
           <v-flex xs12 sm6>
           </v-flex>
         </v-layout>
