<template>
  <div class="echarts" >
    <v-card flat color="brown darken-4"> <!-- teal lighten-1 -->
      <v-card-text class="py-2">
        <v-layout row class="body-1 white--text text--darken-1"><b>Price chart for {{ keyPairTimeFrame }}</b></v-layout>
      </v-card-text>
    </v-card>
    <IEcharts
      :option="candle_sh"
    />
  </div>
</template>

<script type="text/babel">
import {mapState} from 'vuex'
import IEcharts from 'vue-echarts-v3/src/full.js'
// import IEcharts from 'vue-echarts-v3/src/lite.js'
// import IEcharts from 'vue-echarts/components/ECharts'
import 'echarts/lib/chart/bar'
// import 'echarts/lib/chart/line'
import 'echarts/lib/chart/candlestick'
import 'echarts/lib/component/title'
export default {
  name: 'view',
  components: {
    IEcharts
  },
  props: {
  },
  computed: mapState({
    keyPairTimeFrame: state => state.web3.keyPairTimeFrameList[state.web3.keyPairTimeFrameListSelected].pairtf,
    candle_sh: state => {
      // TODO: all are red - need to fix it
      let oldTicks = state.web3.AllTicksList
      let rawData = []
      for (var i = 0; i < oldTicks.length; i++) {
        // rawData.push([oldTicks[i].key, oldTicks[i].O, oldTicks[i].H, oldTicks[i].L, oldTicks[i].C])
        rawData.push([oldTicks[i].key, oldTicks[i].O, oldTicks[i].C, oldTicks[i].H, oldTicks[i].L])
      }
      let dates = rawData.map(function (item) {
        return item[0]
      })
      let data = rawData.map(function (item) {
        return [+item[1], +item[2], +item[3], +item[4]]
      })
      // console.log('newTicks', newTicks)
      return {
        backgroundColor: '#4e342e',
        legend: {
          data: ['Price bar'],
          inactiveColor: '#777',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            animation: false,
            type: 'cross',
            lineStyle: {
              color: '#376df4',
              width: 2,
              opacity: 1
            }
          }
        },
        xAxis: {
          type: 'category',
          data: dates,
          axisLine: { lineStyle: { color: '#8392A5' } }
        },
        yAxis: {
          scale: true,
          axisLine: { lineStyle: { color: '#8392A5' } },
          splitLine: { show: false },
          boundaryGap: [0.01, 0.01]
        },
        grid: {
          bottom: 80
        },
        dataZoom: [{
          textStyle: {
            color: '#8392A5'
          },
          // handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
          handleSize: '80%',
          dataBackground: {
            areaStyle: {
              color: '#8392A5'
            },
            lineStyle: {
              opacity: 0.8,
              color: '#8392A5'
            }
          },
          handleStyle: {
            color: '#fff',
            shadowBlur: 3,
            shadowColor: 'rgba(0, 0, 0, 0.6)',
            shadowOffsetX: 2,
            shadowOffsetY: 2
          }
        },
        {
          type: 'inside'
        }],
        animation: false,
        series: [
          {
            type: 'candlestick',
            name: 'Price bar',
            data: data,
            itemStyle: {
              normal: {
                color: '#69F0AE', // '#0CF49B',
                color0: '#FF5252', // '#FD1050',
                borderColor: '#69F0AE', // '#0CF49B',
                borderColor0: '#FF5252' // '#FD1050'
              }
            }
          }
        ]
      }
    }
  })
}
</script>
<style scoped>
  .echarts {
    position: relative;
    max-height: 500px;
    min-height: 100px;
    height: 300px;
    /*height: 100%;*/
    width: 100%;
  }
</style>



