import Vue from 'vue'
import Vuetify from 'vuetify'
import Vuex from 'vuex'
import state from './state'
import { getWeb3, newAccount, addAccountToLocalStorage } from '../util/getWeb3'
import { pollWeb3, updateState, updateEvents, updateBetState, updateAwardEvents } from '../util/pollWeb3'
// import getContract from '../util/getContract' // import contracts instance
import getGTAContract from '../util/getGTAContract' // import GTA contract instance
import getECBContract from '../util/getECBContract' // import EtherCallBet contract instance
import getOTContract from '../util/getOTContract' // import EtherCallBet contract instance
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuex)
Vue.use(Vuetify)

let GetSelectedAccountLocalStorage = function (storageName) {
  let _acc = null
  let _priv = null
  let accountKeysStr = localStorage.getItem(storageName)
  if (accountKeysStr !== null) {
    let accountKeys = JSON.parse(accountKeysStr)
    if (accountKeys.accounts.length > 0) {
      let accSelected = accountKeys.selectedAccount
      if (accSelected >= 0) {
        _acc = accountKeys.accounts[accSelected].adr
        _priv = accountKeys.accounts[accSelected].prv
      }
    }
  }
  return { _acc, _priv }
}

export const store = new Vuex.Store({
  strict: true,
  state,
  mutations: {
    registerWeb3Instance (state, payload) {
      console.log('registerWeb3instance Mutation being executed', payload)
      let result = payload
      let web3Copy = state.web3
      console.log('result.coinbase=', result.coinbase)
      web3Copy.networkId = result.networkId
      web3Copy.coinbase = result.coinbase
      web3Copy.balance = parseInt(result.balance, 10)
      web3Copy.isInjected = result.injectedWeb3
      web3Copy.web3Instance = result.web3
      state.web3 = web3Copy
      console.log('mutation registerWeb3Instance calling pollWeb3()')
      let selectedAccPriv = GetSelectedAccountLocalStorage('ethercall2')
      if (selectedAccPriv._acc !== null) {
        console.log('call setWeb3AccountPrivate payload = ', selectedAccPriv)
        state.web3.coinbase = selectedAccPriv._acc
        state.web3.account_input = selectedAccPriv._acc
        state.web3.private_input = selectedAccPriv._priv
        state.web3.coinbaseselected = true
        updateState()
      }
      pollWeb3()
    },
    pollWeb3Instance (state, payload) {
      console.log('pollWeb3Instance mutation being executed', payload)
      state.web3.coinbase = payload.coinbase
      // console.log('pollWeb3Instance payload', payload)
      state.web3.balance = parseInt(payload.balance, 10)
      state.web3.account_input = payload.account_input
    },
    registerGTAContractInstance (state, payload) {
      console.log('GTA contract instance: ', payload)
      state.GTAcontractInstance = () => payload
    },
    registerECBContractInstance (state, payload) {
      console.log('ECB contract instance: ', payload)
      state.ECBcontractInstance = () => payload
    },
    registerOTContractInstance (state, payload) {
      console.log('OT contract instance: ', payload)
      state.OTcontractInstance = () => payload
    },
    setWeb3AccountPrivateMutation (state, payload) {
      console.log('Call setWeb3AccountPrivate to', payload.AccountAddress, payload.AccountPrivate)
      if (payload.AccountAddress !== '') {
        state.web3.coinbase = payload.AccountAddress
      }
      state.web3.account_input = payload.AccountAddress
      state.web3.private_input = payload.AccountPrivate
      if (payload.AccountAddress !== '') {
        state.web3.coinbaseselected = true
      } else {
        state.web3.coinbaseselected = false
      }
    },
    getNewBalanceMutation (state) {
      console.log('Call mutation getNewBalanceMutation')
      updateState()
      updateBetState()
      updateEvents()
      updateAwardEvents()
    },
    setAccountsListMutation (state, payload) {
      console.log('start setAccountsListMutation', payload)
      state.web3.AccountsList = []
      for (var i = 0; i < payload.length; i++) {
        console.log('setAccountsListMutation', payload)
        state.web3.AccountsList.push({account: payload[i].adr, account_index: payload[i].prv})
      }
    },
    setGTAbalanceMutation (state, payload) {
      console.log('start setGTAbalanceMutation', payload)
      state.web3.GTAbalance = payload
    },
    setBetGTAbalanceMutation (state, payload) {
      console.log('start setBetGTAbalanceMutation', payload)
      state.web3.BetGTAbalance = payload
    },
    setGTAtoECBallowanceMutation (state, payload) {
      console.log('start setGTAtoECBallowanceMutation', payload)
      state.web3.GTAtoECBallowance = payload
    },
    setUpperStakesMutation (state, payload) {
      console.log('start setUpperStakesMutation', payload)
      state.web3.UpperStake = payload
    },
    setLowerStakesMutation (state, payload) {
      console.log('start setLowerStakesMutation', payload)
      state.web3.LowerStake = payload
    },
    setUpperAllStakesMutation (state, payload) {
      console.log('start setUpperAllStakesMutation', payload)
      state.web3.UpperAllStakes = payload
    },
    setLowerAllStakesMutation (state, payload) {
      console.log('start setLowerAllStakesMutation', payload)
      state.web3.LowerAllStakes = payload
    },
    writeTransactionMutation (state, payload) {
      console.log('writeTransactionMutation', payload)
      let _list = {txHash: payload[0], txType: payload[1], GtaValue: payload[2], EthValue: payload[3]}
      state.web3.TransactionsList.unshift(_list)
    }, // , [hash, _type, _gta, _eth])
    setLastKnownBlockNumberMutation (state, payload) {
      console.log('setLastKnownBlockNumberMutation', payload)
      state.web3.LastKnownBlockNumber = payload
    },
    setAwardLastKnownBlockNumberMutation (state, payload) {
      console.log('setAwardLastKnownBlockNumberMutation', payload)
      state.web3.AwardLastKnownBlockNumber = payload
    },
    writeBetsUpMutation (state, payload) {
      state.web3.BetsUp = payload
    },
    writeBetsDownMutation (state, payload) {
      state.web3.BetsDown = payload
    },
    writeAwardsMutation (state, payload) {
      for (var i = 0; i < payload.length; i++) {
        state.web3.Awards.unshift(payload[i])
      }
    },
    writeBetKeyMutation (state, payload) {
      state.web3.betKey = payload
    },
    writeAllTicksListMutation (state, payload) {
      // console.log('start writeAllTicksListMutation', payload)
      // Need only unique ticks
      let found = false
      if (state.web3.AllTicksList.length !== 0) {
        for (var i = 0; i < state.web3.AllTicksList.length; i++) {
          if (state.web3.AllTicksList[i].key === payload.key) {
            // console.log(state.web3.AllTicksList[i].key, '===', payload.key)
            state.web3.AllTicksList[i].O = payload.O
            state.web3.AllTicksList[i].H = payload.H
            state.web3.AllTicksList[i].L = payload.L
            state.web3.AllTicksList[i].C = payload.C
            found = true
            break
          }
        }
      }
      if (!found) {
        state.web3.AllTicksList.push(payload)
      }
    },
    writeBet (state, payload) {
      if (state.web3.keyPairTimeFrameListSelected !== payload[0]) {
        state.web3.AllTicksList = []
        state.web3.keyPairTimeFrameListSelected = payload[0]
        state.web3.keyPairTimeFrameListSelectedcomplex = payload[1]
      }
    },
    writekeyPairTimeFrameListSelectedMutation (state, payload) {
      console.log('writekeyPairTimeFrameListSelectedMutation', payload)
      if (state.web3.keyPairTimeFrameListSelected !== payload[0]) {
        state.web3.AllTicksList = []
        state.web3.keyPairTimeFrameListSelected = payload[0]
        state.web3.keyPairTimeFrameListSelectedcomplex = payload[1]
      }
    },
    writeBetKeyStateMutation (state, payload) {
      state.web3.betKeyStakeOpened = payload[0]
      state.web3.betKeyStakeClosed = payload[1]
      let resBetState = 'bet closed'
      if (state.web3.betKeyStakeOpened) {
        if (!state.web3.betKeyStakeClosed) {
          resBetState = 'bet open'
        }
      }
      state.web3.keyPairTimeFrameList[state.web3.keyPairTimeFrameListSelected].state = resBetState
    },
    writeUpdateStateTimerStateMutation (state, payload) {
      state.updateStateTimer = payload
    },
    writeUpdateEventsTimerStateMutation (state, payload) {
      state.updateEventsTimer = payload
    },
    writeUpdateBetStateTimerStateMutation (state, payload) {
      state.updateBetStateTimer = payload
    },
    writeUpdateAwardEventsTimerStateMutation (state, payload) {
      state.updateAwardEventsTimer = payload
    }
  },
  actions: {
    registerWeb3 ({commit}) {
      console.log('registerWeb3 Action being executed')
      getWeb3.then(result => {
        console.log('committing result to registerWeb3Instance mutation')
        commit('registerWeb3Instance', result)
      }).catch(e => {
        console.log('error in action registerWeb3', e)
      })
    },
    pollWeb3 ({commit}, payload) {
      console.log('pollWeb3 action being executed')
      commit('pollWeb3Instance', payload)
    },
    /* getContractInstance ({commit}) {
      console.log('executing action getContractInstance')
      getContract.then(result => {
        commit('registerContractInstance', result)
      }).catch(e => console.log(e))
    }, */
    getGTAContractInstance ({commit}) {
      console.log('executing action getGTAContractInstance')
      getGTAContract.then(result => {
        commit('registerGTAContractInstance', result)
      }).catch(e => console.log(e))
    },
    getECBContractInstance ({commit}) {
      console.log('executing action getECBContractInstance')
      getECBContract.then(result => {
        commit('registerECBContractInstance', result)
      }).catch(e => console.log(e))
    },
    getOTContractInstance ({commit}) {
      console.log('executing action getOTContractInstance')
      getOTContract.then(result => {
        commit('registerOTContractInstance', result)
      }).catch(e => console.log(e))
    },
    setWeb3AccountPrivate ({commit}, payload) {
      console.log('Call action setWeb3Private to', payload)
      commit('setWeb3AccountPrivateMutation', payload)
      commit('getNewBalanceMutation')
    },
    getNewBalance ({commit}) {
      console.log('Call action getNewBalance ')
      commit('getNewBalanceMutation')
    },
    setAccountsList ({commit}, payload) {
      commit('setAccountsListMutation', payload)
    },
    setGTAbalance ({commit}, payload) {
      commit('setGTAbalanceMutation', payload)
    },
    setBetGTAbalance ({commit}, payload) {
      commit('setBetGTAbalanceMutation', payload)
    },
    setGTAtoECBallowance ({commit}, payload) {
      commit('setGTAtoECBallowanceMutation', payload)
    },
    setUpperStakes ({commit}, payload) {
      commit('setUpperStakesMutation', payload)
    },
    setLowerStakes ({commit}, payload) {
      commit('setLowerStakesMutation', payload)
    },
    setUpperAllStakes ({commit}, payload) {
      commit('setUpperAllStakesMutation', payload)
    },
    setLowerAllStakes ({commit}, payload) {
      commit('setLowerAllStakesMutation', payload)
    },
    newAccount ({commit}) {
      let payload = newAccount()
      let res = addAccountToLocalStorage(payload[0], payload[1])
      let _accountdata = res[0]
      let AccountAddress = res[1]
      let AccountPrivate = res[2]
      commit('setAccountsListMutation', _accountdata)
      commit('setWeb3AccountPrivateMutation', {AccountAddress, AccountPrivate})
      commit('getNewBalanceMutation')
      // commit('newAccountMutation', payload)
    },
    writeTransaction ({commit}, payload) {
      console.log('writeTransaction', payload)
      commit('writeTransactionMutation', payload)
    },
    setLastKnownBlockNumber ({commit}, payload) {
      console.log('setLastKnownBlockNumber', payload)
      commit('setLastKnownBlockNumberMutation', payload)
    },
    setAwardLastKnownBlockNumber ({commit}, payload) {
      console.log('setAwardLastKnownBlockNumber', payload)
      commit('setAwardLastKnownBlockNumberMutation', payload)
    },
    writeBetsUp ({commit}, payload) {
      commit('writeBetsUpMutation', payload)
    },
    writeBetsDown ({commit}, payload) {
      commit('writeBetsDownMutation', payload)
    },
    writeAwards ({commit}, payload) {
      commit('writeAwardsMutation', payload)
    },
    writeBetKey ({commit}, payload) {
      commit('writeBetKeyMutation', payload)
    },
    writeAllTicksList ({commit}, payload) {
      commit('writeAllTicksListMutation', payload)
    },
    writekeyPairTimeFrameListSelected ({commit}, payload) {
      commit('writekeyPairTimeFrameListSelectedMutation', payload)
    },
    writeBetKeyState ({commit}, payload) {
      commit('writeBetKeyStateMutation', payload)
    },
    writeUpdateStateTimerState ({commit}, payload) {
      commit('writeUpdateStateTimerStateMutation', payload)
    },
    writeUpdateEventsTimerState ({commit}, payload) {
      commit('writeUpdateEventsTimerStateMutation', payload)
    },
    writeUpdateBetStateTimerState ({commit}, payload) {
      commit('writeUpdateBetStateTimerStateMutation', payload)
    },
    writeUpdateAwardEventsTimerState ({commit}, payload) {
      commit('writeUpdateAwardEventsTimerStateMutation', payload)
    }
  }
})



