<template>
  <v-dialog v-model="newAccountDialog" persistent max-width="700px" @keydown.esc="dialog = false">
    <v-card>
     <v-card-title>
       <v-card-text><v-icon x-large color="blue darken-1">info</v-icon><b>You created a new Ethereum account.</b></v-card-text>
       <v-card-text>Account address: <code>{{input_account}}</code></v-card-text>
       <v-card-text><v-icon small color="red darken-1">warning</v-icon><b>To control your account, you need the Private Key. SAVE it now!</b></v-card-text>
       <v-card-text>Private Key: <code>{{input_private}}</code></v-card-text>
       <v-card-text>If you lose the Private Key, you cannot restore it.</v-card-text>
     </v-card-title>
     <v-card-actions>
       <v-spacer></v-spacer>
       <v-btn color="blue darken-1" flat @click.native="newAccountDialog = false">Ok</v-btn>
     </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import {mapState} from 'vuex'
export default {
  data: () => ({
    newAccountDialog: false
  }),
  computed: mapState({
    input_account: state => state.web3.account_input,
    input_private: state => state.web3.private_input
  }),
  methods: {
    open: function () {
      var vm = this
      vm.newAccountDialog = true
      this.$store.dispatch('newAccount')
    },
    clickAccountInput (event) {
      this.newAccountDialog = false
    }
  }
}
</script>



