import Web3 from 'web3'
import {ECBAddress, ECBABI} from './constants/ECBContract'
import {SelectedProvider} from '../util/constants/networks'

let getECBContract = new Promise(function (resolve, reject) {
  let web3 = window.web3
  web3 = new Web3(new Web3.providers.HttpProvider(SelectedProvider))
  let ECBContract = web3.eth.contract(ECBABI)
  let ECBContractInstance = ECBContract.at(ECBAddress)
  console.log('ECBContractInstance!!!!', ECBContractInstance)
  resolve(ECBContractInstance)
})

export default getECBContract



