import Web3 from 'web3'
import {OTAddress, OTABI} from './constants/OTContract'
import {SelectedProvider} from '../util/constants/networks'

let getOTContract = new Promise(function (resolve, reject) {
  let web3 = window.web3
  web3 = new Web3(new Web3.providers.HttpProvider(SelectedProvider))
  let OTContract = web3.eth.contract(OTABI)
  let OTContractInstance = OTContract.at(OTAddress)
  console.log('OTContractInstance!!!!', OTContractInstance)
  resolve(OTContractInstance)
})

export default getOTContract



