import Web3 from 'web3'
// import web3utils from 'web3-utils'
import ethereumjswallet from 'ethereumjs-wallet'
import state from '../store/state'
import {SelectedProvider} from '../util/constants/networks'

/*
* 1. Check for injected web3 (mist/metamask)
* 2. If metamask/mist create a new web3 instance and pass on result
* 3. Get networkId - Now we can check the user is connected to the right network to use our dApp
* 4. Get user account from metamask
* 5. Get user balance
*/

let newAccount = function () {
  let newAccInst = ethereumjswallet.generate()
  return [ newAccInst.getAddressString(), newAccInst.getPrivateKeyString() ]
}

let addAccountToLocalStorage = function (AccountAddress, AccountPrivate) {
  // console.log('AccountAddress', AccountAddress, 'AccountPrivate', AccountPrivate)
  let AccountStore = JSON.parse(localStorage.getItem('ethercall2'))
  let _accountdata
  let newaccountdata = {
    adr: AccountAddress,
    prv: AccountPrivate
  }
  var foundIndex = -1
  if (AccountStore !== null) {
    // There is account store
    for (var i = 0; i < AccountStore.accounts.length; i++) {
      console.log('AccountStore.accounts[i].adr', AccountStore.accounts[i].adr)
      if (AccountStore.accounts[i].adr === newaccountdata.adr) {
        foundIndex = i
        break
      }
    }
    if (foundIndex === -1) {
      AccountStore.accounts.push(newaccountdata)
      foundIndex = AccountStore.accounts.length - 1
    }
    console.log('findIndex', foundIndex)
    // AccountStore = {'accounts': [newaccountdata], selectedAccount: foundIndex}
    if (AccountStore.accounts[foundIndex].prv !== newaccountdata.prv) {
      AccountStore.accounts[foundIndex].prv = newaccountdata.prv
    }
    AccountStore.selectedAccount = foundIndex
    localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
    // Update list to the app store
    // this.$store.dispatch('setAccountsList', AccountStore.accounts)
    // Set selected account to recently added
    AccountAddress = AccountStore.accounts[foundIndex].adr
    AccountPrivate = AccountStore.accounts[foundIndex].prv
    // this.$store.dispatch('setWeb3AccountPrivate', {AccountAddress, AccountPrivate})
    _accountdata = AccountStore.accounts
    return [ _accountdata, AccountAddress, AccountPrivate ]
  } else {
    // There is no account store
    console.log('empty storage, filling...')
    foundIndex = 0
    // console.log('findIndex')
    AccountStore = {'accounts': [newaccountdata], selectedAccount: foundIndex}
    localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
    // Update list to the app store
    // this.$store.dispatch('setAccountsList', [newaccountdata])
    // Set selected account to recently added
    AccountAddress = newaccountdata.adr
    AccountPrivate = newaccountdata.prv
    _accountdata = [newaccountdata]
    // this.$store.dispatch('setWeb3AccountPrivate', {AccountAddress, AccountPrivate})
    return [ _accountdata, AccountAddress, AccountPrivate ]
  }
}

let getWeb3 = new Promise(function (resolve, reject) {
  // Check for injected web3 (mist/metamask)
  var web3js = window.web3
  if (typeof web3js !== 'undefined') {
    var web3 = new Web3(web3js.currentProvider)
    resolve({
      injectedWeb3: web3.isConnected(),
      web3 () {
        return web3
      }
    })
  } else {
    web3 = new Web3(new Web3.providers.HttpProvider(SelectedProvider))

    let web3Connected = false
    console.log('web3.isConnected() start')
    /* web3Connected = */
    web3.net.getListening(function (error1, result2) {
      console.log('web3.isConnected() stop result2=', result2)
      if (result2 === true) {
        console.log('-Network connected!')
        resolve({
          injectedWeb3: result2 /* web3.isConnected() */,
          web3 () {
            // console.log('web3 connected! is', web3, 'result.coinbase', web3.coinbase)
            return web3
          }
        })
      } else {
        console.log('web3', web3, 'web3.isConnected()', result2)
        reject(new Error('Unable to connect to Metamask :()'))
      }
    })
    /* web3Connected = web3.isConnected( function(error, result){
      return result
    }) */
    console.log('web3.isConnected() stop web3Connected = ', web3Connected)
    /* if ( web3.isConnected() === true) {
      console.log('-Network connected!')
      resolve({
        injectedWeb3: web3Connected,
        web3 () {
          // console.log('web3 connected! is', web3, 'result.coinbase', web3.coinbase)
          return web3
        }
      })
    } else {
      console.log('web3', web3, 'web3.isConnected()', web3.isConnected())
      reject(new Error('Unable to connect to Metamask :()'))
    } */
  }
})
  .then(result => {
    return new Promise(function (resolve, reject) {
      // Retrieve network ID
      result.web3().version.getNetwork((err, networkId) => {
        if (err) {
          // If we can't find a networkId keep result the same and reject the promise
          reject(new Error('Unable to retrieve network ID'))
        } else {
          console.log('networkId ->', networkId, 'result.coinbase', result.coinbase)
          // Assign the networkId property to our result and resolve promise
          result = Object.assign({}, result, {networkId})
          resolve(result)
        }
      })
    })
  })
  .then(result => {
    return new Promise(function (resolve, reject) {
      // Retrieve coinbase
      var accountKeysStr = localStorage.getItem('ethercall2')
      // if exists in localstorage get it
      console.log('Read from local storage.')
      if (accountKeysStr !== null /* && typeof web3js !== 'undefined' */) {
        console.log('accountKeysStr ->', accountKeysStr)

        /*
        AccountStore.selectedAccount = foundIndex
        localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
        // Update list to the app store
        // this.$store.dispatch('setAccountsList', AccountStore.accounts)
        // Set selected account to recently added
        AccountAddress = AccountStore.accounts[foundIndex].adr
        AccountPrivate = AccountStore.accounts[foundIndex].prv
        */

        var accountKeys = JSON.parse(accountKeysStr)
        console.log('accountKeys ->', accountKeys)
        console.log('accountKeys.adr ->', accountKeys.adr, 'accountKeys.prv ->', accountKeys.prv)

        var coinbase = accountKeys.accounts[accountKeys.selectedAccount].adr
        state.coinbase = accountKeys.accounts[accountKeys.selectedAccount].adr
        state.private_key = accountKeys.accounts[accountKeys.selectedAccount].prv
        console.log('getWeb3 from local storage account=', state.coinbase, 'private key=', state.private_key)
        result = Object.assign({}, result, { coinbase })
        // console.log('result.coinbase', result.localCoinbase)
        resolve(result)
      } else {
        console.log('Some problem with local storage. Unable to retrieve coinbase.', 'accountKeysStr', accountKeysStr)
        // result = Object.assign({}, result, { coinbase })
        // resolve(result)
        result.web3().eth.getCoinbase((err, coinbase) => {
          if (err) {
            reject(new Error('Unable to retrieve coinbase', coinbase))
          } else {
            console.log('good result.web3().eth.getCoinbase', coinbase)
            result = Object.assign({}, result, { coinbase })
            resolve(result)
          }
        })
      }
    })
  })
  .then(result => {
    return new Promise(function (resolve, reject) {
      // Retrieve balance for coinbase
      console.log('result.web3().eth.getBalance result.coinbase=', result.coinbase)
      result.web3().eth.getBalance(result.coinbase, (err, balance) => {
        if (err) {
          reject(new Error('Unable to retrieve balance for address: ' + result.coinbase))
        } else {
          console.log('ETH balance', balance)
          result = Object.assign({}, result, { balance })
          resolve(result)
        }
      })
    })
  })

export { getWeb3, newAccount, addAccountToLocalStorage }



