import Web3 from 'web3'
import {gtaAddress, gtaABI} from './constants/GTAContract'
import {SelectedProvider} from '../util/constants/networks'

let getGTAContract = new Promise(function (resolve, reject) {
  let web3 = window.web3
  web3 = new Web3(new Web3.providers.HttpProvider(SelectedProvider))
  let GtaContract = web3.eth.contract(gtaABI)
  let GtaContractInstance = GtaContract.at(gtaAddress)
  resolve(GtaContractInstance)
})

export default getGTAContract



